export default {
	data() {
		return {
			id:0,
			detail:{}
		}
	},
	onLoad(options){
		console.log(options)
		this.id=options.id;
		this.getNewsDetail()
	},
	methods: {
		async getNewsDetail(){
		const res = await this.$myRequest({
				url:"/api/getnew/"+this.id
			})
			this.detail=res.data.message[0]
		}
	 
	}
}
